import React from 'react';

import Inputs from '../';
import { formatNombreAvecZero } from '../../../../helpers/helpers';

/**
 *  Exemple minimal d'utilisation
 *  
 *  <InputRpps
 *  label="label"
 *  errorMessage="Message d'erreur !"
 *  />
 */

/**
 * 
 * @description Affiche un input avec son label et sa PopUp d'erreur 
 * 
 * @param {string}   label              Sera envoy au HOC qui affichera le label et le message d'erreur
 * @param {string}   customClass        Sera envoy au HOC qui ajoutera une custom class
 * @param {Number}   height             Sera envoy au HOC qui grera la hauteur du message d'erreur
 * @param {string}   content            Donne qui initialisera la valeur de l'input
 * @param {string}   type               Type d'input (text, email, ...)
 * @param {string}   placeholder        Affichera le placeholder en cas de champ vide
 * @param {string}   errorMessage       Affichera le message d'erreur dans la PopUp
 * @param {number}   maxlength          Ajoutera l'attribus maxlength  l'input
 * @param {number}   minlength          Ajoutera l'attribus minlength  l'input
 * @param {boolean}  required           Affichera le message d'erreur dans la PopUp prcisant que le champ est obligatoire
 * @param {Function} onChange       Sera trigger  chaque saisie dans le champ
 * @param {Function} onKeyPress     Sera trigger lorsque la touche Entre et Tab sera appuy
 * @param {Function} onBlur         Sera trigger au moment du blur de l'element
 * @param {Function} onFocus        Sera trigger au moment du Focus de l'lment
 * @param {Function} validation         Sera utilise pour valider le champ
 * 
 */

export default (props :any) => {

    function formatRPPS(rpps :string) {

        return formatNombreAvecZero(rpps);
    }

    function validRpps(val :string) {
        // On ne vrifie pas la longueur de la chaine, il n'y aura pas de PopUp d'erreur
        return true;
    }

    return (
        <Inputs.Generic
            Input_type="text"
            Input_name="rpps"
            Input_maxlength={20}
            Input_validation={validRpps}
            Input_onChange={({ value }) => formatRPPS(value)}
            {...props}
        />
    );
};
